	--tw-gradient-to: rgb(43, 183, 218);
	--tw-text-opacity: 1;
	color: rgba(250, 250, 250, var(--tw-text-opacity));
	transition-property: all;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
