	opacity: 0.8;
}
.navLink{
	position: relative;
	--tw-gradient-from: rgb(49, 211, 92);
	--tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to, rgba(49, 211, 92, 0));
	--tw-gradient-to: rgb(43, 183, 218);
	padding-top: 0.75rem;
	padding-bottom: 0.75rem;
	--tw-text-opacity: 1;
	color: rgba(150, 152, 166, var(--tw-text-opacity));
	transition-property: all;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	transition-duration: 150ms;
}
.navLink::after{
	content: "";
	position: absolute;
	content: "";
	bottom: -1.25rem;
	content: "";
	left: 0px;
	content: "";
	height: 0.25rem;
	content: "";
	width: 100%;
	content: "";
	background-color: transparent;
}
.navLink:hover{
	--tw-text-opacity: 1;
	color: rgba(0, 0, 0, var(--tw-text-opacity));
}
